.hero-section {
  background: linear-gradient(135deg, #2d5016 0%, #4a7c59 100%);
  color: #fff;
  padding: 5rem 2rem;
  text-align: center;
  position: relative;
}

.hero-section h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero-section p {
  font-size: 1.3rem;
  opacity: 0.9;
}

.founder-bio, .mission-values, .community-commitment {
  padding: 5rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.founder-bio h2, .mission-values h2, .community-commitment h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
  color: #2d5016;
}

.bio-content {
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: 2rem;
  align-items: center;
}

.bio-content img {
  width: 100%;
  border-radius: 10px;
}

.bio-text h3 {
  font-size: 2rem;
  color: #4a7c59;
  margin-bottom: 0.5rem;
}

.bio-text p {
  margin-bottom: 1rem;
  line-height: 1.6;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.value-card {
  background: #f8f9fa;
  padding: 2rem;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.value-card h3 {
  color: #4a7c59;
  margin-bottom: 1rem;
}

.community-commitment {
  background: #f8f9fa;
  text-align: center;
  margin-bottom: 5rem;
}

.community-commitment p {
  font-size: 1.2rem;
  max-width: 800px;
  margin: 0 auto;
  line-height: 1.6;
}

.animated-circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
}

.circle {
  position: absolute;
  border-radius: 50%;
}

.circle1 {
  width: 80px;
  height: 80px;
  background: rgba(74, 124, 89, 0.15);
  top: 10%;
  left: 5%;
  animation: bounce1 10s ease-in-out infinite;
}

.circle2 {
  width: 60px;
  height: 60px;
  background: rgba(45, 80, 22, 0.2);
  top: 30%;
  right: 10%;
  animation: bounce2 12s ease-in-out infinite;
}

.circle3 {
  width: 40px;
  height: 40px;
  background: rgba(107, 142, 90, 0.18);
  bottom: 20%;
  left: 15%;
  animation: bounce3 8s ease-in-out infinite;
}

.circle4 {
  width: 50px;
  height: 50px;
  background: rgba(58, 100, 48, 0.12);
  top: 50%;
  right: 20%;
  animation: bounce4 14s ease-in-out infinite;
}

.circle5 {
  width: 88px;
  height: 88px;
  background: rgba(90, 130, 70, 0.16);
  bottom: 10%;
  left: 50%;
  animation: bounce5 9s ease-in-out infinite;
}

.circle6 {
  width: 55px;
  height: 55px;
  background: rgba(74, 124, 89, 0.14);
  top: 15%;
  left: 20%;
  animation: bounce1 10s ease-in-out infinite;
}

.circle7 {
  width: 65px;
  height: 65px;
  background: rgba(45, 80, 22, 0.18);
  top: 35%;
  right: 5%;
  animation: bounce2 12s ease-in-out infinite;
}

.circle8 {
  width: 45px;
  height: 45px;
  background: rgba(107, 142, 90, 0.16);
  bottom: 25%;
  left: 25%;
  animation: bounce3 8s ease-in-out infinite;
}

.circle9 {
  width: 85px;
  height: 85px;
  background: rgba(58, 100, 48, 0.13);
  top: 55%;
  right: 25%;
  animation: bounce4 14s ease-in-out infinite;
}

.circle10 {
  width: 95px;
  height: 95px;
  background: rgba(90, 130, 70, 0.17);
  bottom: 5%;
  left: 60%;
  animation: bounce5 9s ease-in-out infinite;
}

.circle11 {
  width: 58px;
  height: 58px;
  background: rgba(74, 124, 89, 0.15);
  top: 5%;
  left: 70%;
  animation: bounce1 10s ease-in-out infinite;
}

.circle12 {
  width: 72px;
  height: 72px;
  background: rgba(45, 80, 22, 0.19);
  top: 45%;
  left: 10%;
  animation: bounce2 12s ease-in-out infinite;
}

@keyframes bounce1 {
  0%, 100% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  25% {
    transform: translateY(-40px) translateX(30px) rotate(90deg);
  }
  50% {
    transform: translateY(-20px) translateX(-20px) rotate(180deg);
  }
  75% {
    transform: translateY(-60px) translateX(10px) rotate(270deg);
  }
}

@keyframes bounce2 {
  0%, 100% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  20% {
    transform: translateY(-50px) translateX(-25px) rotate(72deg);
  }
  40% {
    transform: translateY(-30px) translateX(35px) rotate(144deg);
  }
  60% {
    transform: translateY(-10px) translateX(-15px) rotate(216deg);
  }
  80% {
    transform: translateY(-45px) translateX(20px) rotate(288deg);
  }
}

@keyframes bounce3 {
  0%, 100% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  33% {
    transform: translateY(-35px) translateX(25px) rotate(120deg);
  }
  66% {
    transform: translateY(-15px) translateX(-30px) rotate(240deg);
  }
}

@keyframes bounce4 {
  0%, 100% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  25% {
    transform: translateY(-55px) translateX(-20px) rotate(90deg);
  }
  50% {
    transform: translateY(-25px) translateX(40px) rotate(180deg);
  }
  75% {
    transform: translateY(-45px) translateX(-10px) rotate(270deg);
  }
}

@keyframes bounce5 {
  0%, 100% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  20% {
    transform: translateY(-40px) translateX(15px) rotate(72deg);
  }
  40% {
    transform: translateY(-20px) translateX(-35px) rotate(144deg);
  }
  60% {
    transform: translateY(-50px) translateX(25px) rotate(216deg);
  }
  80% {
    transform: translateY(-30px) translateX(-5px) rotate(288deg);
  }
}

@media (max-width: 768px) {
  .bio-content {
    grid-template-columns: 1fr;
  }
}